#header {
    box-shadow: 0px 0px 10px gray;
    border-radius: 1em;
}

#header h1 {
    margin: 0;
    padding: 16px;
}


#footer {
    text-align: center;
    box-shadow: 0px 0px 10px gray;
    border-radius: 1em;
}

#footer p {
    margin: 0;
    padding: 16px;
}
.content {
}

.visible {
    transition: all 0.25s;
    visibility: visible;
    opacity: 100;
    translate: 0px 0px;
    z-index: 1;
}

.hidden {
    transition: all 0.25s;
    visibility: hidden;
    opacity: 0;
    translate: 25px 0px;
    z-index: 0;
}
form {
    display: flex;
    flex-direction: column;
    padding: 16px;
    grid-gap: 32px;
    gap: 32px;
    box-shadow: 0px 0px 5px gray;
    border-radius: 8px;
}

fieldset {
    display: flex;
    flex-direction: column;
    grid-gap: 16px;
    gap: 16px;
}

.label-group {
    display: flex;
    flex-wrap: wrap;
    grid-gap: 24px;
    gap: 24px;
}

label {
    display: flex;
    flex-direction: column;
}
#curriculum {
    display: grid;
    grid-template-rows: 1fr 5fr 1fr;
    grid-template-columns: 1fr 3fr;
    padding: 16px;
    box-shadow: 0px 0px 5px gray;
    border-radius: 8px;
}

#c-header {
grid-column: 1 / 3;
}

#c-sidebar {

}

#c-body {

}

#button-panel {
    grid-column: 1 / 3;
    display: flex;
    align-items: center;
    justify-content: center;
}
#main-container {
    padding: 10px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    height: 100vh;
}
